import React, { Component, Fragment } from "react";
import styled from "styled-components";
import * as _ from "ramda";

const ContainerWrap = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  -webkit-perspective: 150px;
  perspective: 150px;

  .womenWrapper {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    will-change: transform;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
  }

  .womanItem {
    width: 100vw;
    outline: 1px solid ${props => props.theme.green};
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: translate3d(0, 0, 0);

    h1 {
      color: ${props => props.theme.green};
      text-transform: uppercase;
    }
  }
`;

const Container = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid yellow;
`;

const InnerWrap = styled.div`
  margin-top: ${props =>
    props.order ? `calc((-100vh / 3) * ${props.order});` : "0"};
  transform: ${props =>
    props.scroll ? `translate3d(0px, -${props.scroll}px, 0px)` : null};
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
  }
};

class WomenList extends Component {
  state = {
    scrollTop: 0
  };

  scrollCounter = 0;

  componentDidMount() {
    if (typeof window === "undefined") {
      return 0;
    } else if (typeof window != "undefined") {
      if (document.getElementById("womanList")) {
        const womanListHeight = document.getElementById("womanList")
          .offsetHeight;
        document.body.style.height = womanListHeight + "px";
      }
    }
  }

  componentWillUnmount() {
    if (typeof window === "undefined") {
      return 0;
    } else if (typeof window != "undefined") {
      document.removeEventListener("scroll", this._onScroll);
    }
  }

  _onScroll = e => {
    const bodyScrollTop =
      window.pageYOffset !== undefined
        ? window.pageYOffset
        : (
            document.documentElement ||
            document.body.parentNode ||
            document.body
          ).scrollTop;

    this.setState({ scrollTop: bodyScrollTop });
  };

  render() {
    if (typeof window === "undefined") {
      return 0;
    } else if (typeof window != "undefined") {
      document.addEventListener("scroll", this._onScroll);
    }

    console.log("this.scrolltop is , ", this.scrollCounter);

    if (this.props.women) {
      const allWomen = this.props.women.map(woman => {
        return (
          <div className="womanItem" key={woman.id}>
            <h1>{woman.name}</h1>
          </div>
        );
      });

      const containers = (
        <ContainerWrap>
          <Container>
            <InnerWrap
              order={0}
              scroll={this.state.scrollTop}
              id="womanList"
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrap>
          </Container>
          <Container>
            <InnerWrap
              order={1}
              scroll={this.state.scrollTop}
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrap>
          </Container>
          <Container>
            <InnerWrap
              order={2}
              scroll={this.state.scrollTop}
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrap>
          </Container>
        </ContainerWrap>
      );

      return containers;
    } else return null;
  }
}

export default WomenList;
